.features {
  position: relative;
  margin-bottom: 189px;

  @include tablet {
    margin-bottom: 32px;
  }

  &__container {
    justify-content: flex-end;

    @include small-tablet {
      justify-content: center;
    }
  }

  &__background {
    position: absolute;
    top: 0;
    left: -570px;

    @include tablet {
      width: 50%;
    }
  }

  &__foto {
    padding-top: 98px;
    width: 32%;
    z-index: 1;


    @include mobile {
      padding-top: 25px;
      width: 49%;
    }
  }

  &__img {
    object-fit: cover;
  }

  &__title {
    max-width: 438px;
    letter-spacing: -1px;
  }

  &__text-side {
    margin-left: 93px;
    padding-top: 36px;
    width: 42.5%;

    @include small-tablet {
      margin-left: 55px;
    }

    @include mobile {
      margin-left: 15px;
      padding-top: 19px;
    }
  }

  &__text {
    opacity: 0.7;
    max-width: 475px;
    margin-bottom: 72px;
    margin-top: 26px;

    @include small-tablet {
      font-size: 12px;
      margin-bottom: 12px;
    }

    @include mobile {
      margin-top: 0px;
      font-size: 11px;
      line-height: 19px;
    }
  }
}

.options {

  margin-left: 60px;

  @include small-tablet {
    margin-left: 20px;
  }

  @include mobile {
    margin-left: -120px;

  }

  &__list {
    margin: 0;
    display: inline-flex;
  }

  &__items {
    padding-left: 21px;
    position: relative;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 4px;
    text-transform: uppercase;

    @include small-tablet {
      font-size: 9px;
    }

    &:not(:last-child) {
      padding-bottom: 56px;

      @include small-tablet {
        padding-bottom: 10px;
      }

      @include mobile {
        padding-bottom: 0;

      }
    }


    &_hundread::before {
      top: -13px;
      left: -60px;
      position: absolute;
      content: "";
      width: 54px;
      height: 54px;
      border-radius: 34px;
      background-color: var(--color-metall);
      background-image: url("../img/hundread.png");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 40%;

      @include small-tablet {
        top: -3px;
        left: -14px;
        width: 32px;
        height: 32px;
      }
    }

    &_bag::before {
      top: -13px;
      left: -60px;
      position: absolute;
      content: "";
      width: 54px;
      height: 54px;
      border-radius: 34px;
      background-color: var(--color-metall);
      background-image: url("../img/bag.png");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 33%;

      @include small-tablet {
        top: -3px;
        left: -14px;
        width: 32px;
        height: 32px;
      }
    }
  }
}
