.works {
  margin-bottom: 248px;

  @include big-desktop {
    margin-bottom: 129px;
  }

  @include tablet {
    margin-bottom: 32px;
  }

  &__container {
    .tag {
      margin-bottom: 35px;

      @include mobile {
        margin-bottom: 15px;
      }
    }
  }

  &__heading {
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }
  }

  &__nav {
    margin-right: 5px;
    max-width: 45%;

    @include big-desktop {
      max-width: 65%;
    }

    @include small-tablet {
      max-width: 75%;
    }
  }

  &__title {
    display: inline-block;
    margin-bottom: 108px;

    @include tablet {
      margin-bottom: 32px;
    }

    @include mobile {
      margin-bottom: 13px;
    }
  }
}

.works-nav {

  &__list {
    @include small-tablet {
      margin-top: 0;
    }
  }

  &__link {
    @include small-tablet {
      font-size: 12px;
    }
  }

  &__item {
    vertical-align: top;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 4px;
    display: inline-block;

    &:not(:last-child) {
      padding-right: 22px;

      @include small-tablet {
        padding-right: 16px;
      }
    }
  }

  &__link {
    color: var(--color-darkgray);
    opacity: 0.3;
    transition-duration: .1s;
    transition: all .3s;

    &:hover {
      font-weight: 500;
      color: var(--color-blue);
      opacity: 1;
      transition: all .3s;
    }
  }
}

.gallery {
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;


  &__btn {
    padding-top: 2px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 65px;
    height: 65px;
    background-color: var(--color-white);
    opacity: 0;
    transition: all .3s ease-in-out;


    svg {
      fill: var(--color-red);
      transition: all .3s;
    }

    &:hover svg {
      fill: var(--color-blue);
      transition: all .3s;
    }
  }

  &__item {
    width: 31.5%;
    max-height: 450px;
    margin: 0 2.7% 2.6% 0;
    overflow: hidden;
    position: relative;
    font-size: 0;
    transition: all .3s;

    @include mobile {
      width: 47%;
      margin: 0;

    }

    &:hover {
      transform: translateY(-20px);
      transition: all .45s;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }

    &:hover .gallery__btn {
      opacity: 1;
      transition: all .45s;
    }

  }

}

.our-works {
  margin-top: 20px;
  justify-content: space-between;

  @include small-tablet {
    margin-top: 0;
  }

  @include mobile {
    flex-direction: column;

  }

  &__txt {
    max-width: 66%;

    @include small-tablet {
      font-size: 13px;
      line-height: 21px;
    }

    @include mobile {
      font-size: 11px;
      line-height: 18px;
      max-width: 100%;
      padding-bottom: 10px;
    }
  }

  &__btn {
    max-width: 30%;
    padding: 14px 34px 14px 34px;

    @include tablet {
      align-self: flex-end;
    }

    @include small-tablet {
      font-size: 9px;
      line-height: 15px;
      padding: 12px 12px 11px 12px;
    }

    @include mobile {
      align-self: center;
      max-width: 60%;
    }
  }

}
