.services {
  margin-bottom: 174px;

  @include big-desktop {
    margin-bottom: 49px;
  }

  @include small-tablet {
    margin-bottom: 24px;
  }

  &__container {
    position: relative;

    .tag {
      margin-bottom: 35px;

      @include big-desktop {
        margin-left: 66px;
      }

      @include tablet {
        margin-left: 0px;
      }


      @include mobile {
        margin-bottom: 10px;
      }

    }
  }

  &__title {
    max-width: 50%;
    letter-spacing: -1px;

    @include small-tablet {
      max-width: 80%;
    }
  }

  &__band {
    justify-content: space-between;
    margin-bottom: 99px;

    @include big-desktop {
      justify-content: center;
      margin-bottom: 49px;
    }

    @include tablet {
      justify-content: flex-start;
    }

    @include small-tablet {
      flex-direction: column;
      margin-bottom: 10px;
    }

  }

  &__btn-prev {
    margin-right: 20px;
  }

  &__btn-next,
  &__btn-prev {
    width: 50px;
    height: 50px;
    left: 0;
    right: 0;
    position: relative;
    display: inline-block;
    top: 100%;
    bottom: 0;
    background-color: var(--color-smoke);
    transition: .4s;

    @include small-tablet {
      width: 40px;
      height: 40px;
    }

    &:hover {
      background-color: var(--color-red);
      transition: .4s;
    }

    &:active {
      background-color: var(--color-red);
      opacity: 0.21;
      transition: .4s;
    }

  }

  &__btn-next:after,
  &__btn-prev:after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--color-white);
    font-size: 16px;

  }


}

.sub-nav {
  flex-grow: 1;
  max-width: 37%;

  @include small-tablet {
    max-width: 100%;
  }

  &__item {
    max-width: 231px;
    align-self: flex-start;
    margin: 7px;
    flex: 1 0 25%;
    margin-bottom: 13px;

    @include big-desktop {
      max-width: 143px;
    }

    @include small-tablet {
      max-width: 22%;
    }

    @include mobile {
      margin: 0;
    }
  }

  &__list {
    max-height: 192px;
    margin: 0;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;

    @include small-tablet {
      flex-direction: row;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__link {
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    text-align: left;
    transition: .3s;
    opacity: 0.5;

    @include small-tablet {
      font-size: 14px;
    }

    &:hover {
      font-weight: 500;
      color: var(--color-blue);
      opacity: 1;
      transition: .3s;
    }
  }

}

.swiper-container__services {

  margin-left: 0;
  width: 1235px;
  height: 500px;

  @include tablet {
    width: 935px;
  }

  @include mobile {
    width: 447px;
    height: 270px;
  }

}

.swiper-container__services .swiper-slide {

  transition: all .7s;
  opacity: 0.3;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.swiper-slide video {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-container__services .swiper-slide-active {
  opacity: 1;
  transition: all .7s;
}

.services-videos {
  margin-bottom: 72px;

  @include small-tablet {
    margin-bottom: 32px;
  }
}


.slide-1 {
  background-image: url("../img/gallery.png");
}

.slide-2 {
  background-image: url("../img/gallery1.png");
}

.slide-3 {
  background-image: url("../img/gallery1.png");
}

.swiper-slide-prev[data-swiper-slide-index="0"],
.swiper-slide-prev[data-swiper-slide-index="1"],
.swiper-slide-prev[data-swiper-slide-index="2"] {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.swiper-slide-active {
  transition: opacity 0.5s ease-in-out;
}
