.testimonials-swiper {
  width: 100%;
  // height: 100%;
  overflow: hidden;
}

.testimonials {
  margin-bottom: 143px;

  @include big-desktop {
    margin-bottom: 49px;
  }


  @include tablet {
    margin-bottom: 9px;
  }

  &__wrapper {
    justify-content: space-between;

    @include tablet {
      flex-direction: column-reverse;

    }
  }

  &__description {
    max-width: 49.09%;
    flex-direction: column;
    justify-content: space-between;

    @include tablet {
      width: 100%;
      max-width: 81%;
    }

    @include small-tablet {
      max-width: 69%;
    }

  }

  &__tag {
    margin-bottom: 73px;

    @include tablet {
      margin-bottom: 13px;
    }

    @include small-tablet {
      margin-bottom: 0;
    }
  }

  &__title {
    margin-bottom: 121px;

    @include tablet {
      line-height: 40px;
      margin-bottom: 11px;
    }

    @include small-tablet {
      line-height: 25px;
      margin-bottom: 0;
    }

  }

  &__personality {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 13px;

    @include tablet {
      font-size: 14px;
      line-height: 4;
    }

  }

  &__spec {
    margin-bottom: 132px;

    @include tablet {
      margin-bottom: 22px;

    }
  }

  &__avatar-1,
  &__avatar-2,
  &__avatar-3 {
    width: 40.4%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;

    @include tablet {
      height: 595px;
      width: 100%;
      background-position: 0 -83px;
    }

    @include mobile {
      height: 404px;
      background-size: cover;
      background-position: 0 -36px;

    }

  }

  &__avatar-1 {
    background-image: url("../img/selffoto-1.png");
  }

  &__avatar-2 {
    background-image: url("../img/selffoto-2.png");
  }

  &__avatar-3 {
    background-image: url("../img/selffoto-3.png");
  }

  &__btn-prev {
    margin-right: 21px;
  }

  &__btn-next,
  &__btn-prev {
    width: 50px;
    height: 50px;
    left: 0;
    right: 0;
    position: relative;
    display: inline-block;
    top: 100%;
    bottom: 25px;
    background-color: var(--color-smoke);
    transition: .4s;

    @include tablet {
      margin-bottom: 0px;
      left: 83%;
      right: 0;
      bottom: 55px;
    }

    @include small-tablet {
      width: 40px;
      height: 40px;
      left: 72%;
    }

    @include mobile {
      width: 35px;
      height: 35px;
      left: 63%;
      bottom: 48px;
    }


    &:hover {
      background-color: var(--color-red);
      transition: .4s;
    }

    &:active {
      background-color: var(--color-red);
      opacity: 0.21;
      transition: .4s;
    }

  }

  &__btn-next:after,
  &__btn-prev:after {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: var(--color-white);
    font-size: 16px;

  }
}
