.hero {
  margin-top: 142px;
  margin-bottom: 137px;
  height: 840px;
  position: relative;


  @include big-desktop {
    height: 700px;
    overflow: hidden;
    margin-bottom: 67px;
  }

  @include small-tablet {
    margin-top: 84px;
    height: 550px;
    margin-bottom: 0px;

  }

  &__container {
    // flex-direction: column;
    // flex-wrap: wrap;
    // justify-content: space-between;
    position: relative;
    padding-left: calc((100% - 1110px) / 2);
  }


  &__get-started {
    z-index: 5;
    flex-shrink: 0;
    margin-top: 28px;
    margin-bottom: 185px;
    margin-right: 95px;
    width: 474px;

    @include big-desktop {
      margin-left: 90px;
      max-width: 337px;
    }

    @include small-tablet {
      margin-top: 8px;
      margin-bottom: 135px;
      margin-right: 80px;
      max-width: 207px;
    }

    @include mobile {
      margin-right: 8px;
      max-width: 186px;

    }


  }

  &__placeholder-bg {
    position: absolute;
    left: 51%;
    width: calc(100% - 474px - 95px);
    min-height: 840px;
    background-image: url("../img/hero-background.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    @include tablet {
      width: calc(100% - 274px - 95px);
      min-height: 540px;
    }

    @include small-tablet {
      left: 0;
      width: 100%;
      min-height: 440px;
      z-index: 0;
    }
  }

  &__title {
    font-size: 60px;
    font-weight: 700;
    line-height: 70px;
    letter-spacing: -1px;
    text-align: left;
    margin-bottom: 25px;
    margin-top: 40px;

    @include big-desktop {
      font-size: 52px;
      line-height: 60px;
    }

    @include tablet {
      margin-top: 30px;
      font-size: 32px;
      line-height: 40px;
    }

    @include small-tablet {
      margin-top: 10px;
      font-size: 22px;
      line-height: 30px;
    }
  }

  &__descr {
    opacity: 0.7;
    max-width: 462px;
    padding-bottom: 58px;

    @include big-desktop {
      padding-bottom: 20px;
    }

    @include tablet {
      max-width: 262px;
    }

    @include small-tablet {
      max-width: 242px;
      font-size: 12px;
      font-weight: 400;
      line-height: 22px;
    }

  }

  &__video {
    position: relative;
    margin-top: -245px;
    margin-left: calc((100% - 1000px) / 2);
    @include flex-h-center;

    @include tablet {
      margin-left: calc((100% - 720px) / 2);
    }

    @include small-tablet {
      margin-top: -145px;
      margin-left: calc((100% - 220px) / 2);
    }
  }



  &__pag {
    counter-reset: pag;
    display: flex;
    width: 316px;
    font-size: 0;
    // .swiper-pagination {
    //   align-items: center;
    //   // display: flex;
    //   justify-content: center;
    //   width: 316px;
    //   height: 22px;
    //   text-align: left;
    //   background-color: transparent;
    //   // opacity: 1;

    //   // &::before {
    //   //   font-size: 0;
    //   //   counter-increment: pag;
    //   //   content: "0"counter(pag);
    //   // }

    &::after {
      left: 20px;
      top: 12px;
      content: "";
      width: 82%;
      position: absolute;
      background-color: var(--color-beards);
      height: 2px;

      @include tablet {
        width: 72%;
      }

      @include mobile {
        display: none;
      }

    }

    // }
  }

  .swiper-pagination {
    max-width: 20%;

    @include big-desktop {
      left: 7%;
    }

    @include small-tablet {
      left: -38%;
      max-width: 47%;
    }

    @include mobile {
      display: none;
    }

    &-current {
      font-size: 18px;
      display: flex;
      position: relative;
      width: 100%;


      &.active {
        &::after {
          content: "";
          position: absolute;
          left: 19px;
          top: 12px;
          width: 140px;
          height: 2px;
          background-color: var(--color-wood);
          transform: scaleX(1);
          z-index: 10;
          transform-origin: left;
          transition: transform 0.3s ease-in-out;

          @include big-desktop {
            width: 85px;
          }

          @include tablet {
            width: 59px;
          }
        }
      }
    }

    // &.hero-pag {
    //   width: 100%;
    // }

    &-total {
      font-size: 18px;
    }
  }
}

.swiper-pagination-total:before {
  content: "0"
}

.swiper-pagination-current:before {
  content: "0"
}

