.swiper-loader {
  overflow: hidden;
  width: 400px;
  height: 310px;
  transition-duration: .1s;
  transition: all .3s;

  @include tablet {
    width: 300px;
    height: 265px;
  }

  @include mobile {
    width: 240px;
    height: 225px;
  }

  &__img {
    height: 310px;
  }

  &__btn {
    padding-top: 4px;
    padding-right: 0px;
    position: absolute;
    top: 49.6%;
    left: 50.2%;
    transform: translate(-50%, -50%);
    width: 61px;
    height: 55px;
    background-color: var(--color-white);
    transition: all .3s;

    @include small-tablet {
      width: 51px;
      height: 45px;
    }

    @include mobile {
      width: 40px;
      height: 35px;
    }

    svg {
      fill: var(--color-red);
      transition: all .3s;
    }

    &:hover svg {
      fill: var(--color-blue);
      transition: all .3s;
    }
  }
}

.loader {
  max-width: 346px;
  justify-content: space-between;

  &__wrapper {
    display: block;
    height: 15px;
    width: 278px;
    position: relative;
  }

  &__line {
    width: 278px;
    height: 2px;
    background-color: var(--color-beards);
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;

    &_selected {
      position: absolute;
      display: block;
      z-index: 2;
      bottom: 0px;
      width: 139px;
      height: 2px;
      background-color: var(--color-wood);
    }
  }

  &__video {
    width: 100%;
  }
}
