.about {
  margin-bottom: 132px;
  justify-content: space-between;

  @include big-desktop {
    margin-bottom: 22px;
  }


  @include mobile {
    margin-bottom: 15px;
  }

  &__container {
    @include mobile {
      flex-direction: column;
    }
  }

  &__about-img {
    max-width: 475px;
    max-height: 564px;
    height: 564px;

    @include big-desktop {
      width: 55%;
    }

    @include mobile {
      width: 100%;
      max-height: 164px;
    }
  }

  &__descr {
    padding: 35px 0 98px 95px;
    text-align: left;

    &-wrapper {
      flex: 1 0 auto;
    }

    @include big-desktop {
      padding-top: 6px;
      padding-left: 44px;
      padding-bottom: 28px;
      display: flex;
      flex-direction: column;
    }

    @include small-tablet {
      padding-left: 24px;
      padding-bottom: 10px;
    }

    @include mobile {
      padding-left: 0;
    }
  }


  &__caption {
    max-width: 540px;
    padding-bottom: 27px;

    @include tablet {
      max-width: 222px;
    }

    @include small-tablet {
      padding-left: 0px;
    }


    @include mobile {
      width: 100%;
      padding-bottom: 5px;
    }
  }

  &__text {
    max-width: 445px;
    opacity: 0.7;
    padding: 0 0 18px 0;

    @include big-desktop {
      max-width: 305px;
    }

    @include tablet {
      font-size: 16px;
      line-height: 23px;
    }

    @include small-tablet {
      font-size: 13px;
    }

    @include mobile {
      font-size: 11px;
    }
  }

  &__user {
    padding-top: 41px;
    flex: 0 0 auto;

    @include small-tablet {
      padding-top: 0px;
    }
  }

  &__expert {
    opacity: 0.7;
  }

  &__name {
    font-size: 22px;
    font-weight: 500;

    @include small-tablet {
      font-size: 16px;
      font-weight: 400;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__wrap {
    margin-top: 6px;
    font-weight: 400;
    padding-left: 30px;
    font-size: 18px;

    @include small-tablet {
      margin-top: 5px;
      font-size: 13px;
    }

    @include mobile {
      font-size: 11px;
    }
  }


  &__avatar {
    border-radius: 28px;
    background-color: var(--color-metall);
  }

}
