.footer {
  // position: relative;
  height: 300px;
  background-color: var(--color-gray);

  @include big-desktop {
    height: 220px;
  }

  @include mobile {
    height: 260px;
  }

  &__nav {
    margin-left: 26px;
    max-width: 52%;


    @include tablet {
      margin-left: 8px;
    }

    @include mobile {
      max-width: 92%;
    }
  }

  &__top {
    padding-top: 49.5px;
    padding-bottom: 26.5px;
    justify-content: space-between;
    align-items: center;

    @include small-tablet {
      flex-wrap: wrap;
    }

    @include mobile {
      padding-top: 15px;
      flex-direction: column;
      align-items: flex-start;
      padding-bottom: 16px;
    }
  }

  &__bottom {
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    padding: 15.5px 0;

    @include mobile {
      flex-direction: column;
      padding-top: 0;
    }
  }




  &__item {
    display: inline-block;
    min-width: 75px;

    @include tablet {
      min-width: 55px;
    }

    &:not(:last-child) {
      padding-right: 60px;

      @include big-desktop {
        padding-right: 30px;
      }

      @include tablet {
        padding-right: 0px;
      }
    }

  }

  &__link {
    font-size: 18px;
    color: var(--color-white);
    transition-duration: .1s;
    transition: all .3s;

    @include tablet {
      font-size: 12px;
    }

    &:hover {
      transition: all .3s;
      color: var(--color-blue);
    }
  }

  &__mail {
    height: 40px;
    // width: 26%;
    justify-content: space-between;

    @include small-tablet {
      height: 32px;
      width: 30%;
      flex-wrap: wrap;
    }

    @include mobile {
      width: 100%;
      height: auto;
      align-self: center;
      flex-wrap: nowrap;
    }
  }

  &__mail-add {
    font-size: 18px;
    padding: 0;
    padding-left: 20px;
    max-width: 171px;
    height: 40px;
    border: none;

    @include small-tablet {
      height: 32px;
      font-size: 14px;
      max-width: 75%;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__btn-send {
    font-size: 14px;
    vertical-align: bottom;
    color: var(--color-white);
    letter-spacing: 4px;
    font-style: normal;
    font-weight: 500;
    text-transform: uppercase;
    background-color: var(--color-brown);
    padding: 12px 27px;
    transition-duration: .1s;
    transition: all .3s;


    @include small-tablet {
      font-size: 11px;
      width: 75%;
      padding: 9px 27px;
    }

    &:hover {
      transition: all .3s;
      color: var(--color-blue);
      background-color: var(--color-red);
    }
  }

  .social {
    display: flex;
    align-items: center;
    margin-right: 9px;

    &__item:not(:last-child) {
      margin-right: 18.5px;
    }
  }

  &__svg {
    @include mobile {
      align-self: center;
    }
  }
}

.social__link {
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.4;
  transition-duration: .1s;
  transition: all .3s;

  &:hover {
    transition-duration: .1s;
    transition: all .3s;
    opacity: 1;
  }


  &--tw {
    width: 16px;
    height: 14px;
    background-image: url("../img/twitter.png")
  }

  &--in {
    width: 16px;
    height: 16px;
    background-image: url("../img/linkedin.png")
  }

  &--gl {
    width: 14px;
    height: 14px;
    background-image: url("../img/google.png")
  }

  &--fb {
    width: 8px;
    height: 15px;
    background-image: url("../img/facebook.png")
  }

}

.submenu {
  font-size: 18px;
  font-weight: 400;
  color: var(--color-white);

  &__link {
    @include tablet {
      font-size: 12px;
    }
  }


  &__list {
    display: flex;

    @include mobile {
      margin: 10px 0 10px 0;
    }
  }

  &__item {
    transition-duration: .1s;
    transition: all .3s;
    opacity: 0.4;

    &:not(:last-child) {
      margin-right: 42.5px;
    }

    &:hover {
      transition: all .3s;
      opacity: 1;
    }
  }

}
