.clients {
  padding-top: 101px;
  background-color: var(--color-white);

  @include big-desktop {
    padding-top: 10px;
  }

  &__wrapper {
    justify-content: space-between;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__txt {
    opacity: 0.7;
    max-width: 445px;
    margin-bottom: 56px;

    @include big-desktop {
      margin-bottom: 26px;
    }

    @include small-tablet {
      font-size: 14px;
      max-width: 328px;
    }

    @include mobile {
      font-size: 10px;
      line-height: 18px;
    }
  }

  &__cap {
    margin-bottom: 12px;
  }

  &__worked {
    margin-bottom: 20px;

    @include big-desktop {
      margin-bottom: 0px;
      align-self: center;
    }

    @include mobile {
      flex-shrink: 1;
    }
  }

  &__title {
    @include big-desktop {
      align-self: center;
    }

    @include small-tablet {
      align-self: flex-start;
    }

    @include mobile {
      flex-grow: 1;
    }
  }
}

.xiaomi {
  cursor: pointer;
  opacity: 0.15;
  width: 80px;
  height: 63px;
  background-image: url("../img/xiaomi.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: all .4s;


  @include big-desktop {
    width: 40px;
    height: 33px;
  }

  @include mobile {
    width: 24px;
    height: 18px;
  }

  &:hover {
    opacity: 1;
    transition: all .4s;
  }

}

